import { db } from './firebase';
import { 
  collection, 
  doc, 
  addDoc, 
  updateDoc, 
  deleteDoc, 
  getDocs, 
  getDoc, 
  query, 
  where, 
  orderBy
} from 'firebase/firestore';

import { Seance, CreateSeanceData, Film } from '../types';
import { generateId } from '../utils/helpers';

const SEANCES_COLLECTION = 'seances';
const FILMS_COLLECTION = 'films';

/**
 * Cr√©er une nouvelle s√©ance avec g√©n√©ration automatique du QR code
 */
export const createSeance = async (data: CreateSeanceData): Promise<Seance> => {
  try {
    // V√©rifier qu'il y a exactement 5 films
    if (data.films.length !== 5) {
      throw new Error('Une s√©ance doit contenir exactement 5 films');
    }

    // V√©rifier que tous les films existent
    const filmsPromises = data.films.map(filmId => 
      getDoc(doc(db, FILMS_COLLECTION, filmId))
    );
    const filmsSnapshots = await Promise.all(filmsPromises);
    
    const nonExistentFilms = filmsSnapshots
      .map((snapshot, index) => ({ snapshot, filmId: data.films[index] }))
      .filter(({ snapshot }) => !snapshot.exists());
    
    if (nonExistentFilms.length > 0) {
      throw new Error(`Films non trouv√©s: ${nonExistentFilms.map(f => f.filmId).join(', ')}`);
    }

    // G√©n√©rer l'URL du QR code
    const seanceId = generateId();
    const qrCodeUrl = generateQRCodeUrl(seanceId);

    const seanceData: Omit<Seance, 'id'> = {
      nom: data.nom,
      date: data.date,
      heure: data.heure,
      films: data.films,
      qrCodeUrl,
      isActive: true,
      createdAt: new Date(),
      updatedAt: new Date(),
      ...(data.description && { description: data.description }),
    };

    const docRef = await addDoc(collection(db, SEANCES_COLLECTION), seanceData);
    
    return {
      id: docRef.id,
      ...seanceData,
    };
  } catch (error) {
    console.error('Erreur lors de la cr√©ation de la s√©ance:', error);
    throw error;
  }
};

/**
 * R√©cup√©rer toutes les s√©ances
 */
export const getAllSeances = async (): Promise<Seance[]> => {
  try {
    const q = query(
      collection(db, SEANCES_COLLECTION),
      orderBy('date', 'desc')
    );
    
    const querySnapshot = await getDocs(q);
    const seances: Seance[] = [];
    
    querySnapshot.forEach((doc) => {
      const data = doc.data();
      seances.push({
        id: doc.id,
        nom: data.nom,
        description: data.description,
        date: data.date.toDate(),
        heure: data.heure,
        films: data.films,
        qrCodeUrl: data.qrCodeUrl,
        isActive: data.isActive,
        createdAt: data.createdAt.toDate(),
        updatedAt: data.updatedAt.toDate(),
      });
    });
    
    return seances;
  } catch (error) {
    console.error('Erreur lors de la r√©cup√©ration des s√©ances:', error);
    throw error;
  }
};

/**
 * R√©cup√©rer une s√©ance par son ID
 */
export const getSeanceById = async (seanceId: string): Promise<Seance | null> => {
  try {
    const docRef = doc(db, SEANCES_COLLECTION, seanceId);
    const docSnap = await getDoc(docRef);
    
    if (docSnap.exists()) {
      const data = docSnap.data();
      return {
        id: docSnap.id,
        nom: data.nom,
        description: data.description,
        date: data.date.toDate(),
        heure: data.heure,
        films: data.films,
        qrCodeUrl: data.qrCodeUrl,
        isActive: data.isActive,
        createdAt: data.createdAt.toDate(),
        updatedAt: data.updatedAt.toDate(),
      };
    }
    
    return null;
  } catch (error) {
    console.error('Erreur lors de la r√©cup√©ration de la s√©ance:', error);
    throw error;
  }
};

/**
 * R√©cup√©rer une s√©ance avec ses films
 */
export const getSeanceWithFilms = async (seanceId: string): Promise<{ seance: Seance; films: Film[] } | null> => {
  try {
    const seance = await getSeanceById(seanceId);
    if (!seance) return null;

    // R√©cup√©rer tous les films de la s√©ance
    const filmsPromises = seance.films.map(filmId => 
      getDoc(doc(db, FILMS_COLLECTION, filmId))
    );
    const filmsSnapshots = await Promise.all(filmsPromises);
    
    const films: Film[] = [];
    
    for (const snapshot of filmsSnapshots) {
      if (snapshot.exists()) {
        const data = snapshot.data();
        if (data) {
          films.push({
            id: snapshot.id,
            titre: data.titre,
            realisateur: data.realisateur,
            pays: data.pays,
            duree: data.duree,
            annee: data.annee,
            synopsis: data.synopsis,
            posterUrl: data.posterUrl,
            genre: data.genre,
            createdAt: data.createdAt.toDate(),
            updatedAt: data.updatedAt.toDate(),
          });
        }
      }
    }

    return { seance, films };
  } catch (error) {
    console.error('Erreur lors de la r√©cup√©ration de la s√©ance avec films:', error);
    throw error;
  }
};

/**
 * Mettre √† jour une s√©ance
 */
export const updateSeance = async (seanceId: string, data: Partial<CreateSeanceData>): Promise<void> => {
  try {
    const docRef = doc(db, SEANCES_COLLECTION, seanceId);
    
    const updateData: any = {
      ...data,
      updatedAt: new Date(),
    };

    // Si les films changent, v√©rifier qu'il y en a 5
    if (data.films && data.films.length !== 5) {
      throw new Error('Une s√©ance doit contenir exactement 5 films');
    }

    await updateDoc(docRef, updateData);
  } catch (error) {
    console.error('Erreur lors de la mise √† jour de la s√©ance:', error);
    throw error;
  }
};

/**
 * Supprimer une s√©ance
 */
export const deleteSeance = async (seanceId: string): Promise<void> => {
  try {
    const docRef = doc(db, SEANCES_COLLECTION, seanceId);
    await deleteDoc(docRef);
  } catch (error) {
    console.error('Erreur lors de la suppression de la s√©ance:', error);
    throw error;
  }
};

/**
 * Activer/d√©sactiver une s√©ance
 */
export const toggleSeanceActive = async (seanceId: string, isActive: boolean): Promise<void> => {
  try {
    const docRef = doc(db, SEANCES_COLLECTION, seanceId);
    await updateDoc(docRef, {
      isActive,
      updatedAt: new Date(),
    });
  } catch (error) {
    console.error('Erreur lors de la modification du statut de la s√©ance:', error);
    throw error;
  }
};

/**
 * R√©cup√©rer les s√©ances actives
 */
export const getActiveSeances = async (): Promise<Seance[]> => {
  try {
    const q = query(
      collection(db, SEANCES_COLLECTION),
      where('isActive', '==', true),
      orderBy('date', 'desc')
    );
    
    const querySnapshot = await getDocs(q);
    const seances: Seance[] = [];
    
    querySnapshot.forEach((doc) => {
      const data = doc.data();
      seances.push({
        id: doc.id,
        nom: data.nom,
        description: data.description,
        date: data.date.toDate(),
        heure: data.heure,
        films: data.films,
        qrCodeUrl: data.qrCodeUrl,
        isActive: data.isActive,
        createdAt: data.createdAt.toDate(),
        updatedAt: data.updatedAt.toDate(),
      });
    });
    
    return seances;
  } catch (error) {
    console.error('Erreur lors de la r√©cup√©ration des s√©ances actives:', error);
    throw error;
  }
};

/**
 * G√©n√©rer l'URL du QR code pour une s√©ance
 */
export const generateQRCodeUrl = (seanceId: string): string => {
  const baseUrl = window.location.origin;
  const seanceUrl = `${baseUrl}/seance/${seanceId}`;
  
  // Utiliser un service de g√©n√©ration de QR code
  const qrServiceUrl = 'https://api.qrserver.com/v1/create-qr-code/';
  const params = new URLSearchParams({
    size: '300x300',
    data: seanceUrl,
    format: 'png',
  });
  
  return `${qrServiceUrl}?${params.toString()}`;
};

/**
 * R√©cup√©rer les statistiques des s√©ances
 */
export const getSeancesStats = async (): Promise<{
  total: number;
  actives: number;
  inactives: number;
  cetteSemaine: number;
}> => {
  try {
    const seances = await getAllSeances();
    const maintenant = new Date();
    const uneSemaine = new Date(maintenant.getTime() + 7 * 24 * 60 * 60 * 1000);
    
    const stats = {
      total: seances.length,
      actives: seances.filter(s => s.isActive).length,
      inactives: seances.filter(s => !s.isActive).length,
      cetteSemaine: seances.filter(s => s.date >= maintenant && s.date <= uneSemaine).length,
    };
    
    return stats;
  } catch (error) {
    console.error('Erreur lors de la r√©cup√©ration des statistiques:', error);
    throw error;
  }
};

/**
 * Rechercher des s√©ances par nom
 */
export const searchSeances = async (searchTerm: string): Promise<Seance[]> => {
  try {
    const seances = await getAllSeances();
    const term = searchTerm.toLowerCase();
    
    return seances.filter(seance => 
      seance.nom.toLowerCase().includes(term) ||
      (seance.description && seance.description.toLowerCase().includes(term))
    );
  } catch (error) {
    console.error('Erreur lors de la recherche de s√©ances:', error);
    throw error;
  }
}; 